.wrapper {
    position: relative;
    display: block;
    padding: 10em 0em 0em 0em;
    background: url(../../assets/images/homepage-backround.png) no-repeat center;
    background-size: cover;
}
.banner-wrapper {
    padding: 5em 0em;
    background: rgba(0,0,0, 0.8);
}
#header {
    position: relative;
    padding: 5em 0em 24.3em 0em;
}
.container {
    overflow: hidden;
    margin: 0em auto;
    width: 100%; 
}
#banner {
    text-align:center;
}
#banner button {
    margin-top: 2em;
    padding: 1.40em 3em;
    border-radius: 6px;
    font-weight: 700;
    font-size: 1em;
    background: #f3cc2f;
    color: #3f0900;
}
.button {
    display: inline-block;
    padding: 1em 2em 1em 2em;
    letter-spacing: 0.10em;
    margin-top: 2em;
    padding: 1.40em 3em;
    background: #f3cc2f;
    border-radius: 6px;
    text-decoration: none;
    font-weight: 700;
    font-size: 1em;
    color: #3f0900;
}

.button:hover {
    cursor: pointer;
    opacity: 0.7;
}

.title {
   margin-bottom: 2.3em; 
   color: white;
   font-size: 3em;
}
ul.actions li
{
	display: inline;
	padding: 0em 0.50em;
}