.menu {
    top: 4.5em;
    right: 0;
    background-color: #ffffff;
}
.menu ul {
    list-style-type: none;
    width: 200px;
}
.menu li {
    text-align: center;
    padding: 30px 0 1px 16px;
}
.menu li a {
    width: 400px;
    padding: 1em 1.5em;
    letter-spacing: 1px;
    text-decoration: none;
    font-size: 2.6em;
    color: #1a75ff;
}

.Form {
    padding: 15px;
    border: 1px solid #cccccc;
    -webkit-box-shadow: -14px 22px 24px 4px rgba(0,0,0,0.75);
    -moz-box-shadow: -14px 22px 24px 4px rgba(0,0,0,0.75);
    box-shadow: -14px 22px 24px 4px rgba(0,0,0,0.75);
}